// Grid by float
@mixin build-grid($prefix:'fs-') {

  @for $i from 1 through length($grid-value) {
    .#{$prefix}col-#{nth($grid-value, $i)}-lg {
      @include min-query-media(lg) {
        width: convertGrid(#{nth($grid-value, $i)}); }
      @include max-query-media(lg) {
        width: 100%; } }
    .#{$prefix}col-#{nth($grid-value, $i)}-md {
      @include min-query-media(md) {
        width: convertGrid(#{nth($grid-value, $i)}); }
      @include max-query-media(md) {
        width: 100%; } }
    .#{$prefix}col-#{nth($grid-value, $i)}-sm {
      @include min-query-media(sm) {
        width: convertGrid(#{nth($grid-value, $i)}); }
      @include max-query-media(sm) {
        width: 100%; } }
    .#{$prefix}col-#{nth($grid-value, $i)}-xs {
      @include min-query-media(xs) {
        width: convertGrid(#{nth($grid-value, $i)}); } } } }

@include build-grid();
