.fs-burger {
  display: none;
  width: convertRem(27);
  span {
    display: block;
    height: convertRem(2);
    margin: convertRem(7) 0;
    @extend .fs-bg-white; }
  @include max-query-media(sm) {
    display: inline-block; } }

@mixin responsive-nav {
  @include max-query-media(sm) {
    position: fixed;
    width: convertRem(240);
    height: 100%;
    top: 0;
    right: 0;
    background-color: $white;
    @include transition(all .5s);
    @include transform(translateX(convertRem(240)));
    &.open {
      @include transform(translateX(0)); }
    span {
      a {
        color: $black; } } } }

nav {
  &.fs {
    &-nav {
      &--inline {
        @include responsive-nav;
        span {
          @extend .plr20;
          @include max-query-media(sm) {
            display: block; }
          &:first-child {
            @include min-query-media(sm) {
              padding-left: 0; } }
          &:last-child {
            @include min-query-media(sm) {
              padding-right: 0; } } } } } } }
