// Utilities
.fs {
  &-left {
    float: left; }
  &-right {
    float: right; }
  &-block {
    display: block !important;
    &-lg {
      @include min-query-media(lg) {
        display: none !important; }
      @include max-query-media(lg) {
        display: block !important; } }
    &-md {
      @include min-query-media(md) {
        display: none !important; }
      @include max-query-media(md) {
        display: block !important; } }
    &-sm {
      @include min-query-media(sm) {
        display: none !important; }
      @include max-query-media(sm) {
        display: block !important; } }
    &-xs {
      @include min-query-media(xs) {
        display: none !important; }
      @include max-query-media(xs) {
        display: block !important; } } }
  &-hide {
    display: none !important;
    &-lg {
      @include min-query-media(lg) {
        display: block !important; }
      @include max-query-media(lg) {
        display: none !important; } }
    &-md {
      @include min-query-media(md) {
        display: block !important; }
      @include max-query-media(md) {
        display: none !important; } }
    &-sm {
      @include min-query-media(sm) {
        display: block !important; }
      @include max-query-media(sm) {
        display: none !important; } }
    &-xs {
      @include min-query-media(xs) {
        display: block !important; }
      @include max-query-media(xs) {
        display: none !important; } } }
  &-fullpage {
    height: 100vh; }
  &-table {
    display: table;
    width: 100%; }
  &-table-cell {
    display: table-cell; }
  &-inline {
    display: inline-block; }
  &-no-border {
    border: 0; } }

// Color & Background
@mixin color($prefix:'fs-') {
  @for $i from 1 through length($color-name) {
    .#{$prefix}#{nth($color-name, $i)} {
      color: #{nth($color-list, $i)}; }
    .#{$prefix}bg-#{nth($color-name, $i)} {
      background-color: #{nth($color-list, $i)}; } } }
@include color();

// Text Align
@mixin align($prefix:'fs-') {
  @for $i from 1 through length($name) {
    .#{$prefix}ta#{nth($name, $i)} {
      text-align: #{nth($align, $i)}; }
    .#{$prefix}ta#{nth($name, $i)}-xs {
      @include query-media(xs) {
        text-align: #{nth($align, $i)}; } } }
  @for $i from 1 through length($vname) {
    .#{$prefix}ta#{nth($vname, $i)} {
      vertical-align: #{nth($valign, $i)}; } } }
@include align();
