// Variable flexbox width
$width-size : a, b;
// Flex
@mixin build-flex($prefix:'fs-') {

  @for $i from 1 through length($grid-value) {
    .#{$prefix}flex-#{nth($grid-value, $i)}-lg {
      @include min-query-media(lg) {
        flex: none;
        width: convertGrid(#{nth($grid-value, $i)}); }
      @include max-query-media(lg) {
        flex: none;
        width: 100%; } }
    .#{$prefix}flex-#{nth($grid-value, $i)}-md {
      @include min-query-media(md) {
        flex: none;
        width: convertGrid(#{nth($grid-value, $i)}); }
      @include max-query-media(md) {
        flex: none;
        width: 100%; } }
    .#{$prefix}flex-#{nth($grid-value, $i)}-sm {
      @include min-query-media(sm) {
        flex: none;
        width: convertGrid(#{nth($grid-value, $i)}); }
      @include max-query-media(sm) {
        flex: none;
        width: 100%; } }
    .#{$prefix}flex-#{nth($grid-value, $i)}-xs {
      @include min-query-media(xs) {
        flex: none;
        width: convertGrid(#{nth($grid-value, $i)}); }
      @include max-query-media(sm) {
        flex: none;
        width: convertGrid(#{nth($grid-value, $i)}); } } } }

@include build-flex();
